<template>
  <top-bar :title="'游客消费要素占比'" :left="left" :right="right" :max="max" :values="values" />
</template>
<script>
import TopBar from "@/components/charts/TopBar.vue";

export default {
  mounted() {
    this.$getData("/market/factor").then(list => {
      this.list = list;
      this.$unlock();
    });
  },
  data() {
    return {
      list: []
    };
  },
  computed: {
    left() {
      return this.list.map(({ type }) => type);
    },
    values() {
      return this.list.map(({ value }) => Number(value * 100).toFixed(2));
    },
    right() {
      return this.values.map(item => item + "%");
    },
    max() {
      return 100;
    }
  },
  components: {
    TopBar
  }
};
</script>